//  
// import toWatched from './js/add-to-watched'

//    '''toWatched.setQueueOne(obj)'''         .

//     setWatchedOne()    (   )       (LS), 
//    true     false.

//        toQueue.getWatchedOne()   ,   LS   id ,     , 
//      ,      Json  LS   undefined,     LS  false.
 
//       toQueue.getWatchedAll()   
//      ,      Json  LS   undefined,     LS  false.

//     removeWatchedOneEl()    ,    LS      true    , 
//     Json  LS   undefined,     LS  false.


const WATCHED = 'to-watched';

export const toWatched = {

    addMovie(movie, arr) {
        if (arr.every(e => e.id !== movie.id)) {
            arr.push(movie);
        }
        return arr;
    },


    removeMovie(id, arr){
        return arr.filter(e => e.id !== id)
    },


    getWatchedAll() { 
        if (localStorage.getItem(WATCHED)) {
            return JSON.parse(localStorage.getItem(WATCHED));
        }
        return undefined
    },


    getWatchedOne(id){
        if (localStorage.getItem(WATCHED)) {
            const arr = JSON.parse(localStorage.getItem(WATCHED));

            return arr.filter(e => e.id === id)
        }
        return undefined
    },


    setWatchedOne(el) {
    try{
        if (localStorage.getItem(WATCHED)) {
            const arr = JSON.parse(localStorage.getItem(WATCHED));

            localStorage.setItem(WATCHED, JSON.stringify(this.addMovie(el, arr)));
            return true;
        }

        const arrMovie = [];

        arrMovie.push(el);
        localStorage.setItem(WATCHED, JSON.stringify(arrMovie));

        return true
    }catch{
        return false
    }
    },


    removeWatchedOneEl(id){
        try{
        if (localStorage.getItem(WATCHED)) {
            const arr = JSON.parse(localStorage.getItem(WATCHED));

            localStorage.setItem(WATCHED, JSON.stringify(this.removeMovie(id, arr)))
            return true
        }
        return undefined
    }catch{
        return false
    }

    },
}
